@import "https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap";
*, :before, :after {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
}

h2, p {
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  display: flex;
  box-shadow: 1px 2px 4px 1px #0000000d;
}

.header .logo-link {
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
}

.header .nav__list {
  align-items: center;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.header .nav__item {
  text-transform: uppercase;
  position: relative;
}

.header .nav__item:not(:last-child) {
  margin-right: 20px;
}

.header .nav__link {
  text-align: center;
  color: #060b35;
  height: 100%;
  padding: 23px 0;
  text-decoration: none;
  transition: color .25s;
  display: block;
}

.header .nav__link.is-active {
  color: #00acdc;
  transform: scaleX(1);
}

.header .nav__link.is-active:after {
  content: " ";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.header .nav__item:hover .nav__link, .header .nav__item:focus .nav__link {
  color: #00acdc;
}

.catalog {
  justify-content: center;
  align-items: center;
  padding-top: 50px;
  padding-bottom: 72px;
  display: flex;
}

.catalog__card {
  gap: 46px 48px;
  display: grid;
}

@media (width <= 487px) {
  .catalog__card {
    grid-template-columns: 1fr;
  }
}

@media (width >= 488px) and (width <= 767px) {
  .catalog__card {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (width >= 768px) and (width <= 1023px) {
  .catalog__card {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (width >= 1024px) {
  .catalog__card {
    grid-template-columns: repeat(4, 1fr);
  }
}

.stars {
  display: flex;
}

.stars .stars__star {
  background-image: url("star.822d561e.svg");
  background-position: 50%;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars.stars__star:last-child {
  margin: 0;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  transition: transform .3s ease-in;
  transform: scale(1);
}

.card:hover {
  transform: scale(1.2);
}

.card__image {
  width: 160px;
  height: 134px;
  margin-inline: auto;
  display: block;
}

.card_title {
  transition: color .3s ease in;
  color: #060b35;
  margin-top: 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card:hover .card__title {
  color: #34568b;
}

.card__code {
  color: #616070;
  margin-top: 4px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__review {
  color: #060b35;
  justify-content: space-between;
  align-items: flex-end;
  margin-top: 16px;
  font-size: 12px;
  font-weight: 400;
  line-height: 14px;
  display: flex;
}

.card__price {
  color: #616070;
  justify-content: space-between;
  align-items: center;
  margin-top: 24px;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
  display: flex;
}

.card__price .card__price-value {
  text-align: end;
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  text-transform: uppercase;
  transition: background-color .3s ease-in color .3s ease-in;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  margin-top: 16px;
  padding: 11px 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.card__buttonhover, .card__buttonfocus {
  color: #00acdc;
  background-color: #fff;
}
